body {
    padding: 0;
    margin: 0;
}

#deck-view {
    display: grid;
    grid-template-rows: 75px auto;
    grid-template-areas:
        "navbar"
        "content";

    height: 100vh;
    width: 100vw;
}

#navbar {
    grid-area: navbar;

    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: flex-start;

    width: 100%;

    background-color: rgb(0, 0, 0);
}

.navbar-menu {
    padding-left: 0.5em;
    padding-right: 0.5em;
    order: 0;
    /* flex: 0 0 auto; */
}

.navbar-menu button {
    height: 50px;
    
    border-radius: 0;
    background-color: black;
    border-width: 0;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 1em;
}

.navbar-header {
    padding-left: 0.5em;
    padding-right: 0.5em;
    color: white;
    order: 1;
    /* flex: 1 1 auto; */
    
    flex: auto;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.navbar-social {
    padding-left: 0.5em;
    padding-right: 0.5em;
    order: 2;
    margin-left: auto;
    /* flex: 0 0 auto; */

    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

#deck-container {
    grid-area: content;

    display: flex;
    flex-direction: column;
    position: relative;

    height: 100%;
    width: 100%;

    align-items: center;
    justify-content: center;

    background-color: rgb(236, 236, 236);
}

#deck-carousel {
    flex: 0 0 auto;
    display: flex;
    flex-direction: row;

    justify-content: center;

    width: 100%;

    margin-left: 100px;
    margin-right: 100px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.deck {
    position: relative;
    display: grid;
    margin-left: 50px;
    margin-right: 50px;
}

.deck h2 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    justify-self: center;
}

.deck-card:nth-child(1) {
    position: absolute;
    aspect-ratio: 1;
    height: max(150px, 10vh);

    box-shadow: 2px 2px 2px black;
    background-color: white;
    
    z-index: 4;
}

.deck-card:nth-child(2) {
    position: absolute;
    aspect-ratio: 1;
    height: max(150px, 10vh);

    transform: translateX(4px) translateY(3px);

    box-shadow: 2px 2px 2px black;
    background-color: white;

    z-index: 3;
}

.deck-card:nth-child(3) {
    aspect-ratio: 1;
    height: max(150px, 10vh);

    transform: translateX(8px) translateY(6px);

    box-shadow: 2px 2px 2px black;
    background-color: white;

    z-index: 2;
}

#deck-menu {
    flex: 0 0 auto;
    margin-bottom: auto;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;

    top: calc(100% - 60px);

    width: 400px;
    height: 50px;

    border-radius: 25px;
    background-color: rgb(0, 0, 0);
}

#deck-menu > button {
    margin-left: 10px;
    margin-right: 10px;

    width: 45px;
    height: calc(100% - 4px);

    border-radius: 10%;
    border-width: 0;

    color: white;
    background-color: rgb(0, 0, 0);
}

#deck-menu > img {
    margin-left: 10px;
    margin-right: 10px;

    width: 45px;
    height: calc(100% - 4px);

    border-radius: 10%;
    border-width: 0;

    color: white;
    background-color: rgb(0, 0, 0);
}